import { request,getToken,Token } from '../../request.js'
export default {
	data() {
		return {
			baseURL:this.$api,
			shop:[],
			vip:[],
			uid:0,
			currentIndex: 0,
			navList: [
				{
					title: '店家数',
					number: '0人'
				},
				{
					title: 'VIP会员',
					number: '0人'
				}
			],
		}
	},
	onLoad(options){
		let id = options.id;
		this.uid = id;
		this.getList(id);
	},
	methods: {
		getList(id){
			let that = this;
			request({
				url:"index.php?s=/wap/member/apiGetStaffInfo",
				method:"POST",
				data:{
					uid:id
				}
			}).then(function(res){
				if(res.data.code==1){
					that.shop = res.data.shop;
					that.vip = res.data.vip;
					that.navList[0].number = res.data.countshop;
					that.navList[1].number = res.data.countvip;
				}
			});
		},
		// 改变导航
		changeNabIndex(index) {
			this.currentIndex = index
		},
		goSet(id){
			uni.navigateTo({
				url:"/pages/components/staff-info/staff-set?id="+id,
			})
		}
	}
}
